@keyframes BlinkBoxKF {
  0% {
    border-color: #e9fff6;
  }
  20% {
    border-color: #0028f8;
  }
  40% {
    border-color: #f3f3ff;
  }
  60% {
    border-color: #00ff00;
  }
  80% {
    border-color: #790041;
  }
}

.blinkBox {
  box-sizing: content-box;
  position: absolute;
  pointer-events: none;
  border: 1px solid transparent;
  animation: BlinkBoxKF .5s linear;
}
